import { useNavigate, useParams } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Box, Button, FormControl, FormLabel, HStack, Input, Textarea, VStack } from '@chakra-ui/react';

export const BoardUpdatePage = () => {
    const { id } = useParams();
    const [title, setTitle] = useState<string>('');
    const [content, setContent] = useState<string>('');

    const navigate = useNavigate();

    useEffect(() => {
        axios.get(`http://localhost/api/board/${ id }`)
            .then((response) => {
                setTitle(response.data.title);
                setContent(response.data.content);
            });
    }, []);

    const onSubmit = () => {
        axios.put(`http://localhost:8080/api/board/${ id }`, {
            title: title,
            content: content,
        })
            .then((response) => {
                navigate('/');
            });
    };

    const onDelete = () => {
        window.confirm('삭제하시겠습니까?') && (
            axios.delete(`http://localhost:8080/api/board/${ id }`)
                .then((response) => {
                    navigate('/');
                })
        );
    };

    return (
        <Box p={ '20px' } m={ '100px' } borderRadius={ 'lg' } boxShadow={ 'xl' } bg={ 'white' }>
            <VStack spacing={ 10 } align={ 'center' }>
                <FormControl>
                    <FormLabel fontSize={ '30px' } fontFamily={ 'Nanum Gothic' } fontWeight={ 'bold' }>제목</FormLabel>
                    <Input
                        placeholder={ 'Title' }
                        value={ title }
                        onChange={ (e) => setTitle(e.target.value) }
                    />
                </FormControl>
                <FormControl>
                    <FormLabel fontSize={ '25px' } fontFamily={ 'Nanum Gothic' } fontWeight={ 'bold' }>내용</FormLabel>
                    <Textarea
                        placeholder={ 'Content' }
                        value={ content }
                        onChange={ (e) => setContent(e.target.value) }
                    />
                </FormControl>
                <HStack>
                    <Button colorScheme="twitter" onClick={ onSubmit }>
                        제출
                    </Button>
                    <Button colorScheme="red" onClick={ onDelete }>
                        삭제
                    </Button>
                </HStack>
            </VStack>
        </Box>
    );
};